@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

@keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

* {
  font-family: "Nunito", sans-serif; }

body {
  margin: 0px;
  padding: 0px;
  color: #555555; }

a {
  text-decoration: none;
  color: #555555; }

#app {
  position: relative;
  padding: 60px 0px 60px 0px;
  min-height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  opacity: 0;
  -webkit-animation: fade-in 0.5s forwards;
          animation: fade-in 0.5s forwards; }



