@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

@keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

#footer-wrapper {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 60px; }
  #footer-wrapper #footer {
    max-width: 1000px;
    margin: auto;
    padding: 20px; }
    #footer-wrapper #footer #signature {
      display: block;
      font-size: 14px;
      line-height: 20px;
      color: #ffffff;
      text-align: center; }
      #footer-wrapper #footer #signature span {
        color: #f44336; }
      #footer-wrapper #footer #signature a {
        color: #a0a0a0; }



